import _ from 'lodash'
import PopJs from './pop-js'

class SisModel extends PopJs {
  constructor(id) {
    super(id)

    this.modelType = 'SIS'

    this.compartment = {
      susceptible: 0,
      infectious: 0
    }

    this.defaultParams = {
      initPopulation: 50000,
      initPrevalence: 3000,
      recoverRate: 0.1,
      reproductionNumber: 1.5
    }

    this.param = _.cloneDeep(this.defaultParams)

    this.varEvents.push(['susceptible', 'infectious', 'rateForce'])
    this.paramEvents.push(['infectious', 'susceptible', 'recoverRate'])

    this.guiParams = [
      {
        key: 'time',
        value: 100,
        max: 300,
        interval: 1,
        placeHolder: '',
        label: 'Time',
        decimal: 0
      },
      {
        key: 'reproductionNumber',
        value: 1.5,
        max: 20,
        interval: 0.1,
        placeHolder: '',
        label: 'R0',
        decimal: 1
      },
      {
        key: 'durationInfection',
        value: 10,
        interval: 1,
        max: 100,
        placeHolder: '',
        label: 'Infectious Period (days)',
        decimal: 0
      },
      {
        key: 'initPrevalence',
        value: 3000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Prevalence',
        decimal: 0
      },
      {
        key: 'initPopulation',
        value: 50000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Initial Population',
        decimal: 0
      }
    ]

    this.interventionParams = [
      {
        key: 'interventionDay',
        value: 5,
        max: 100,
        interval: 1,
        placeHolder: '',
        label: 'Start Day',
        decimal: 2
      },
      {
        key: 'reproductionNumber',
        value: 1.2,
        interval: 0.01,
        max: 1,
        placeHolder: '',
        label: 'R0',
        decimal: 2
      }
    ]
  }

  calcExtraParams() {
    this.param.recoverRate = 1 / this.param.durationInfection
    this.param.contactRate =
      this.param.reproductionNumber * this.param.recoverRate
  }

  calcVars() {
    this.var.population = _.sum(_.values(this.compartment))
    this.var.rateForce =
      (this.param.contactRate / this.var.population) *
      this.compartment.infectious
    this.var.rn =
      (this.compartment.susceptible / this.var.population) *
      this.param.reproductionNumber
  }
}

class SirModel extends PopJs {
  constructor(id) {
    super(id)

    this.modelType = 'SIR'

    this.compartment = {
      susceptible: 0,
      infectious: 0,
      recovered: 0
    }

    this.defaultParams = {
      initPopulation: 50000,
      initPrevalence: 3000,
      recoverRate: 0.1,
      reproductionNumber: 1.5
    }

    this.varEvents.push(['susceptible', 'infectious', 'rateForce'])
    this.paramEvents.push(['infectious', 'recovered', 'recoverRate'])

    this.param = _.cloneDeep(this.defaultParams)

    this.guiParams = [
      {
        key: 'time',
        value: 100,
        max: 300,
        interval: 1,
        placeHolder: '',
        label: 'Time',
        decimal: 0
      },
      {
        key: 'reproductionNumber',
        value: 1.5,
        interval: 0.1,
        max: 20,
        placeHolder: '',
        label: 'R0',
        decimal: 1
      },
      {
        key: 'durationInfection',
        value: 10,
        interval: 1,
        max: 100,
        placeHolder: '',
        label: 'Infectious Period (days)',
        decimal: 0
      },
      {
        key: 'initPrevalence',
        value: 3000,
        max: 100000,
        placeHolder: '',
        interval: 1,
        label: 'Prevalence',
        decimal: 0
      },
      {
        key: 'initPopulation',
        value: 50000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Initial Population',
        decimal: 0
      }
    ]

    this.interventionParams = [
      {
        key: 'interventionDay',
        value: 5,
        max: 100,
        interval: 1,
        placeHolder: '',
        label: 'Start Day'
      },
      {
        key: 'reproductionNumber',
        value: 1.2,
        interval: 0.01,
        max: 10,
        placeHolder: '',
        label: 'R0'
      }
    ]
  }

  calcExtraParams() {
    this.param.recoverRate = 1 / this.param.durationInfection
    this.param.contactRate =
      this.param.reproductionNumber * this.param.recoverRate
  }

  initCompartmentsByParams() {
    this.compartment.infectious = this.param.initPrevalence
    this.compartment.susceptible =
      this.param.initPopulation - this.param.initPrevalence
    this.compartment.recovered = 0
  }

  calcVars() {
    this.var.population = _.sum(_.values(this.compartment))
    this.var.rateForce =
      (this.param.contactRate / this.var.population) *
      this.compartment.infectious
    this.var.rn =
      (this.compartment.susceptible / this.var.population) *
      this.param.reproductionNumber
  }
}

class SEIRModel extends PopJs {
  constructor(id) {
    super(id)
    this.id = id

    this.modelType = 'SEIR'

    this.compartment = {
      susceptible: 0,
      exposed: 0,
      infectious: 0,
      recovered: 0,
      dead: 0
    }

    this.defaultParams = {
      initPopulation: 50000,
      period: 0.1,
      incubation: 0.01,
      caseFatality: 0.2,
      initPrevalence: 5000,
      reproductionNumber: 4
    }
    this.param = _.cloneDeep(this.defaultParams)

    this.varEvents.push(['susceptible', 'exposed', 'rateForce'])
    this.paramEvents.push(['exposed', 'infectious', 'incubationRate'])
    this.paramEvents.push(['infectious', 'infectious', 'disDeath'])
    this.paramEvents.push(['infectious', 'recovered', 'recoverRate'])
    this.paramEvents.push(['infectious', 'dead', 'deathRate'])

    this.guiParams = [
      {
        key: 'time',
        value: 100,
        max: 300,
        interval: 1,
        placeHolder: '',
        label: 'time',
        decimal: 0
      },
      {
        key: 'reproductionNumber',
        value: 1.5,
        max: 15,
        interval: 0.1,
        placeHolder: '',
        label: 'R0',
        decimal: 1
      },
      {
        key: 'durationInfection',
        value: 10,
        interval: 1,
        max: 100,
        placeHolder: '',
        label: 'Infectious Period (days)',
        decimal: 0
      },
      {
        key: 'caseFatality',
        value: 0.2,
        interval: 0.01,
        max: 1,
        placeHolder: '',
        label: 'Case-Fatality Rate',
        decimal: 2
      },
      {
        key: 'initPrevalence',
        value: 3000,
        interval: 1,
        max: 100000,
        placeHolder: '',
        label: 'Prevalence',
        decimal: 0
      },
      {
        key: 'initPopulation',
        value: 50000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Initial Population',
        decimal: 0
      }
    ]

    this.interventionParams = [
      {
        key: 'interventionDay',
        value: 5,
        interval: 1,
        placeHolder: '',
        label: 'Start Day'
      },
      {
        key: 'reproductionNumber',
        value: 1.2,
        interval: 0.01,
        placeHolder: '',
        label: 'R0'
      }
    ]
  }

  calcExtraParams() {
    this.param.deathRate =
      this.param.caseFatality / this.param.durationInfection
    this.param.recoverRate =
      1 / this.param.durationInfection - this.param.deathRate
    this.param.disDeath = -1 * this.param.caseFatality * this.param.period
    this.param.incubationRate = this.param.incubation
    this.param.contactRate = this.param.reproductionNumber * this.param.period
  }

  calcVars() {
    this.var.population = _.sum(_.values(this.compartment))
    this.var.rateForce =
      (this.param.contactRate / this.var.population) *
      this.compartment.infectious
    this.var.rn =
      (this.compartment.susceptible / this.var.population) *
      this.param.reproductionNumber
  }
}

class SEIRSModel extends PopJs {
  constructor(id) {
    super(id)
    this.id = id

    this.modelType = 'SEIRS'

    this.compartment = {
      susceptible: 0,
      exposed: 0,
      infectious: 0,
      recovered: 0,
      dead: 0
    }

    this.defaultParams = {
      initPopulation: 50000,
      period: 0.1,
      incubation: 0.01,
      caseFatality: 0.2,
      initPrevalence: 3000,
      reproductionNumber: 50,
      immunityPeriod: 50
    }
    this.param = _.cloneDeep(this.defaultParams)

    this.varEvents.push(['susceptible', 'exposed', 'rateForce'])
    this.paramEvents.push(['exposed', 'infectious', 'incubationRate'])
    this.paramEvents.push(['infectious', 'infectious', 'disDeath'])
    this.paramEvents.push(['infectious', 'recovered', 'recoverRate'])
    this.paramEvents.push(['recovered', 'susceptible', 'immunityLossRate'])
    this.paramEvents.push(['infectious', 'dead', 'deathRate'])

    this.guiParams = [
      {
        key: 'time',
        value: 100,
        max: 300,
        interval: 1,
        placeHolder: '',
        label: 'Time',
        decimal: 0
      },
      {
        key: 'reproductionNumber',
        value: 1.5,
        interval: 0.1,
        max: 20,
        placeHolder: '',
        label: 'R0',
        decimal: 1
      },
      {
        key: 'durationInfection',
        value: 10,
        interval: 1,
        placeHolder: '',
        label: 'Infectious Period (days)',
        decimal: 0
      },
      {
        key: 'caseFatality',
        value: 0.2,
        interval: 0.01,
        max: 1,
        placeHolder: '',
        label: 'Case-Fatality Rate',
        decimal: 2
      },
      {
        key: 'immunityPeriod',
        value: 50,
        interval: 1,
        max: 300,
        placeHolder: '',
        label: 'Immunity Period ',
        decimal: 0
      },
      {
        key: 'initPrevalence',
        value: 3000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Prevalence',
        decimal: 0
      },
      {
        key: 'initPopulation',
        value: 50000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Initial Population',
        decimal: 0
      }
    ]

    this.interventionParams = [
      {
        key: 'interventionDay',
        value: 5,
        interval: 1,
        placeHolder: '',
        label: 'Start Day'
      },
      {
        key: 'reproductionNumber',
        value: 1.2,
        interval: 0.01,
        placeHolder: '',
        label: 'R0'
      }
    ]
  }

  calcExtraParams() {
    this.param.deathRate =
      this.param.caseFatality / this.param.durationInfection
    this.param.recoverRate =
      1 / this.param.durationInfection - this.param.deathRate
    this.param.incubationRate = this.param.incubation
    this.param.disDeath = -1 * this.param.caseFatality * this.param.period
    this.param.contactRate = this.param.reproductionNumber * this.param.period
    this.param.immunityLossRate = 1 / this.param.immunityPeriod
  }

  calcVars() {
    this.var.population = _.sum(_.values(this.compartment))
    this.var.rateForce =
      (this.param.contactRate / this.var.population) *
      this.compartment.infectious
    this.var.rn =
      (this.compartment.susceptible / this.var.population) *
      this.param.reproductionNumber
  }
}

class TbModel extends PopJs {
  constructor(id) {
    super(id)
    this.id = id

    this.modelType = 'Ebola'

    this.compartment = {
      susceptible: 0,
      latentEarly: 0,
      latentRemote: 0,
      infectious: 0,
      diagnosed: 0,
      treated: 0,
      dead: 0,
      susceptibleAgain: 0
    }

    this.defaultParams = {
      initPopulation: 5000,
      initPrevalence: 300,
      beta: 5,
      betaAgain: 5,
      latentChangeRate: 1 / 100,
      infectionEarlyRate: 1.1 / 1000,
      infectionLateRate: 5.5 / 1000000,
      diagnosisRate: 1 / 365 / 3,
      treatmentRate: 1 / 14,
      recoverRate: 2 / 365,
      deathRate1: 1 / 365 / 3,
      deathRate2: 1 / 365 / 10
    }

    this.param = _.cloneDeep(this.defaultParams)

    this.varEvents.push(['susceptible', 'latentEarly', 'rateForce'])
    this.varEvents.push(['susceptibleAgain', 'latentEarly', 'rateForceAgain'])
    this.paramEvents.push(['latentEarly', 'latentRemote', 'latentChangeRate'])
    this.paramEvents.push(['latentEarly', 'infectious', 'infectionEarlyRate'])
    this.paramEvents.push(['latentRemote', 'infectious', 'infectionLateRate'])
    this.paramEvents.push(['infectious', 'diagnosed', 'diagnosisRate'])
    this.paramEvents.push(['diagnosed', 'treated', 'treatmentRate'])
    this.paramEvents.push(['infectious', 'dead', 'deathRate1'])
    this.paramEvents.push(['treated', 'dead', 'deathRate2'])
    this.paramEvents.push(['treated', 'susceptibleAgain', 'recoverRate'])

    this.guiParams = [
      {
        key: 'years',
        value: 5,
        max: 20,
        interval: 0.1,
        placeHolder: '',
        label: 'Years',
        decimal: 1
      },
      {
        key: 'caseDetectionRate',
        value: 0.1,
        interval: 0.01,
        placeHolder: '',
        max: 1,
        label: 'Case Detection Rate',
        decimal: 1
      },
      {
        key: 'beta',
        value: 0.5,
        interval: 0.01,
        placeHolder: '',
        max: 10,
        label: 'Contact Rate',
        decimal: 2
      },
      {
        key: 'betaAgain',
        value: 0.5,
        interval: 0.01,
        placeHolder: '',
        max: 10,
        label: 'Contact Rate Again',
        decimal: 2
      },
      {
        key: 'initPopulation',
        value: 5000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Population',
        decimal: 0
      },
      {
        key: 'initPrevalence',
        value: 300,
        max: 10000,
        interval: 1,
        placeHolder: '',
        label: 'Initial Prevalence',
        decimal: 0
      },
    ]

    this.interventionParams = [
      {
        key: 'interventionDay',
        value: 5,
        interval: 1,
        placeHolder: '',
        label: 'Start Day',
        decimal: 2
      }
    ]
  }

  calcExtraParams() {
    this.param.diagnosisRate =
      (this.param.caseDetectionRate / (1 - this.param.caseDetectionRate)) *
      (1 / 365 / 3)
  }

  calcVars() {
    this.var.population = _.sum(_.values(this.compartment))
    this.var.rateForce =
      (this.param.beta * this.compartment.infectious) / this.var.population
    this.var.rateForceAgain =
      (this.param.betaAgain * this.compartment.infectious) / this.var.population
  }
}

let epiModels = [
  {
    Class: TbModel,
    name: 'Tuberculosis'
  },
  {
    Class: SirModel,
    name: 'Susceptible Infectious Recovered'
  },
  {
    Class: SisModel,
    name: 'Susceptible Infectious Susceptible'
  },
  {
    Class: SEIRModel,
    name: 'Susceptible Exposed Infections Recovered'
  },
  {
    Class: SEIRSModel,
    name: 'Susceptible Exposed Infections Recovered Susceptible'
  }
]

export { epiModels }



